import React from 'react';
import { Row, Col } from 'reactstrap';

import BobaIcon from '../../assets/img/Icons/BobaIcon.jpg';
import VolleyballIcon from '../../assets/img/Icons/VolleyballIcon.png';
import XboxIcon from '../../assets/img/Icons/XboxIcon.png';

import JavascriptIcon from '../../assets/img/Icons/JavascriptIcon.png';
import ReactIcon from '../../assets/img/Icons/ReactIcon.png';
import VueIcon from '../../assets/img/Icons/VueIcon.png';
import PythonIcon from '../../assets/img/Icons/PythonIcon.png';
import ExpressIcon from '../../assets/img/Icons/ExpressIcon.png';

import PicMountFuji from '../../assets/img/Japan2019/MountFuji.jpg';
import PicProfilePic2 from '../../assets/img/Backgrounds/ProfilePic2.jpg';

// import PicProfilePic from '../../assets/img/Backgrounds/ProfilePic.JPG';
// import PicMountFuji from '../../assets/img/Backgrounds/MountFuji.jpg';
const HomePage = () => {
  return (
    <div>
      <Row style={{height: '90vh'}}>
        <Col xs="5" style={{marginTop: '15px', position: 'relative'}}>
          <Row style={{marginBottom: '10px'}}>
            <Col xs="12" className="text-center">
              <img src={PicProfilePic2} style={{height: '175px', width: '175px', borderRadius: '50%'}} alt="Profile"></img>
            </Col>
          </Row>
          <Row>
            <Col xs="12" className="text-center">
              <h1>Ryan Louie</h1>
            </Col>
          </Row>
          <Row>
            <Col xs="4" style={{paddingRight: '0px'}}>
              <hr></hr>
            </Col>
            <Col xs="4" className="text-center">
              <img src={VolleyballIcon} style={{height: '30px', width: '30px'}} alt="Volleyball Icon"></img>
              <img src={BobaIcon} style={{height: '30px', width: '30px'}}  alt="Boba Icon"></img>
              <img src={XboxIcon} style={{height: '30px', width: '30px'}}  alt="Xbox Icon"></img>
            </Col>
            <Col xs="4" style={{paddingLeft: '0px'}}>
              <hr></hr>
            </Col>
          </Row>
          <Row>
            <Col xs="12" className="text-center">
              <h2>Full Stack Web Developer</h2>
            </Col>
          </Row>
          <Row style={{position: 'absolute', bottom: 0, marginBottom: '5vh', width: '100%', textAlign: 'center'}} >
            <Col xs="12">
              <a href="https://www.javascript.com/" target="_blank" rel="noopener noreferrer">
                <img className="skill-icon" src={JavascriptIcon} style={{height: '60px', width: '60px'}} alt="JS Icon"></img>
              </a>
              <a href="https://reactjs.org/" target="_blank" rel="noopener noreferrer">
                <img className="skill-icon" src={ReactIcon} style={{height: '60px', width: '60px'}} alt="React Icon"></img>
              </a>
              <a href="https://vuejs.org/" target="_blank" rel="noopener noreferrer">
                <img className="skill-icon" src={VueIcon} style={{height: '60px', width: '60px'}} alt="Vue Icon"></img>
              </a>
              <a href="https://expressjs.com/" target="_blank" rel="noopener noreferrer">
                <img className="skill-icon" src={ExpressIcon} style={{height: '60px', width: '60px'}} alt="Express Icon"></img>
              </a>
              <a href="https://www.python.org/" target="_blank" rel="noopener noreferrer">
                <img className="skill-icon" src={PythonIcon} style={{height: '60px', width: '60px'}} alt="Python Icon"></img>
              </a>
            </Col>
          </Row>
        </Col>
        <Col xs="7" style={{padding: '0px'}}>
          <div style={{backgroundImage: `url(${PicMountFuji})`, height: '100%', backgroundPosition: 'center', backgroundRepeat: 'no-repeat', backgroundSize: 'cover'}}>
          </div>
        </Col>
      </Row>
    </div>
  )
}

export default HomePage;